import React from 'react';
import styled from 'styled-components';
import Input from "../../Atomics/Input";
import Form from "../../Atomics/Form";
import {
    useCreateWithEmailAndPassword,
    useFireBaseFirestore,
    useSignInWithEmailAndPassword
} from "../../Providers/FireBaseProvider";
import {useTransition, animated} from "react-spring";
import Button from "../../Atomics/Button";
import Submit from "../../Atomics/Submit";
import {useAddSnack} from "../../Atomics/Snackbar";

const Base = styled.div`
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    background-color: white;
`;

const Title = styled.div`
    padding: 16px;
    font-size: 16pt;
`;

const Animated = styled(animated.div)`
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const LoginButton = styled(Button)`
    padding: 8px;
    margin: 8px;
    margin-top: 0px;
    font-size: 10pt;
    color: blue;
`;

const Login = () => {
    const [signUp, setSignUp] = React.useState(false);

    const signInWithEmailAndPassword = useSignInWithEmailAndPassword();
    const addSnack = useAddSnack();

    const handleSignIn = (state) => {
        signInWithEmailAndPassword(state.email, state.password).catch(handleError);
    };

    const handleError = (error) => {
        switch(error.code) {
            case 'auth/invalid-email':
                addSnack('Ungültige Email Adresse.')
                break;
            case 'auth/wrong-password':
                addSnack('Falsches Passwort.')
                break;
            case 'missing-surname':
                addSnack('Vorname wird benötigt.');
                break;
            case 'missing-lastname':
                addSnack('Nachname wird benötigt.');
                break;
            case 'auth/network-request-failed':
                addSnack('Es konnte keine Verbindung zum Server hergestellt werden.');
                break;
            case 'auth/weak-password':
                addSnack('Unsicheres Passwort.');
                break;
            case 'auth/argument-error':
                addSnack('Bitte füllen Sie alle Felder aus!');
                break;
            default:
                addSnack(error.code);
        }
    };

    const Firestore = useFireBaseFirestore();
    const createUserWithEmailAndPassword = useCreateWithEmailAndPassword();

    const handleRegistration = (state) => {
        if(!state.lastname) return handleError({code: 'missing-lastname'});
        if(!state.surname) return handleError({code: 'missing-surname'});

        createUserWithEmailAndPassword(state.email, state.password).then((info) => {
            Firestore.collection('Users').doc(info.user.uid).set({
                surname: state.surname,
                lastname: state.lastname,
                roles: {
                    user: true
                },
            })
        }).catch(handleError);
    }

    const transitions = useTransition(signUp, null, {
        from: {transform: 'translateX(-200%) scale(0)', opacity: 0},
        enter: {transform: 'translateX(0%) scale(1)', opacity: 1},
        leave: {transform: 'translateX(200%) scale(0)', opacity: 0}
    });

    const SignIn = () => {
        return (
            <Base>
                <Title>
                    Login
                </Title>
                <Form onSubmit={handleSignIn}>
                    <Input placeholder={'E-Mail Adresse'} name={'email'}/>
                    <Input type={'password'} placeholder={'Passwort'} name={'password'}/>
                    <Submit>Einloggen</Submit>
                </Form>
                <LoginButton text onClick={() => setSignUp(state => !state)}>Registrieren</LoginButton>
            </Base>
        )
    };

    const SignUp = () => {
        return (
            <Base>
                <Title>
                    Registrieren
                </Title>
                <Form onSubmit={handleRegistration}>
                    <Input placeholder={'Vorname'} name={'surname'}/>
                    <Input placeholder={'Nachname'} name={'lastname'}/>
                    <Input placeholder={'eMail Adresse'} name={'email'}/>
                    <Input type={'password'} placeholder={'Passwort'} name={'password'}/>
                    <Submit>Registrieren</Submit>
                </Form>
                <LoginButton text onClick={() => setSignUp(state => !state)}>Login</LoginButton>
            </Base>
        )
    };

    return <div style={{backgroundColor: '#eee', width: '100%', height: '100vh'}}>{transitions.map(({item, props}) =>
        item ?
            (<Animated key={0} style={props}>{SignUp()}</Animated>) :
            (<Animated key={1} style={props}>{SignIn()}</Animated>)
    )}</div>
};

export default Login;