import React from 'react';
import styled from "styled-components";

const Base = styled.form`
    padding: 0px 16px 16px 16px;
    display: flex;
    flex-direction: column;
`;

const Submit = styled.input`
    display: none;
`;

const Form = ({children, onSubmit = () => {}, onChange = () => {}, ...props}) => {
    const [state, setState] = React.useState({});

    const childrenArray = React.Children.toArray(children).filter(item => {
        return item?.type?.name !== 'Submit'
    });

    const submitException = React.Children.toArray(children).filter(item => {
        return item?.type?.name === 'Submit';
    });

    const handleUpdate = (target) => {
        setState(state => {
            const newState = {...state, [target.name]: target.value};
            onChange(newState);
            return newState;
        });
    };

    const handleSubmit = (event) => {
        event.preventDefault();

        onSubmit(state);
    };

    if(Object.entries(state).length !== childrenArray.length) {

        let temp = {};

        childrenArray.forEach(child => {
            if(child.props.name !== undefined) {
                temp = {...temp, [child.props.name]: child.props.value ?? ''};
            }
        });

        onChange(temp);

        setState({...temp});
    };

    return (
        <Base onSubmit={handleSubmit}>
            {childrenArray.map((child, index) => {
                return React.cloneElement(child, {key: index, onChange: (event) => handleUpdate(event.target), value: state?.[child.props.name]})
            })}
            {submitException}
            <Submit type={'submit'} />
        </Base>
    )
};

export default Form;