import React from 'react';
import Button from "./Button";
import styled from 'styled-components';

const Base = styled(Button)`
    margin: 4px;
    padding: 8px;
    background-color: #0d47a1;
    color: white;
    overflow: hidden;
    border-radius: 4px;
    font-size: 10pt;
`;

const Submit = ({children}) => {
    return (
        <Base>
            {children}
        </Base>
    )
};

export default Submit;