import styled from 'styled-components';

const Input = styled.input`
    transition: 0.3s ease all;
    outline: none;
    border: none;
    border-bottom: 2px solid #ccc;
    background-color: white;
    padding: 8px;
    margin: 4px;
    
    :focus {
        border-color: blue;
        background-color: white;
    }
    
    ${props => props.unstyled ? `
        margin: 0;
        padding: 0;
        font-size: inherit;
        font-color: inherit;
        border: none;
    ` : ''}
`;

export default Input;